@import url('https://fonts.googleapis.com/css?family=Oxygen');
@import url('https://fonts.googleapis.com/css?family=Lobster');

$void: #0E0B16;
$fuschia: #A239CA;
$jewel: #4717F6;
$stark: #E7DFDD;

//TEST
svg {
    height: auto;
    width: 50%;
}

//HIDDEN SECTIONS
#projects, #instructions, #contact, #skills, #about {
    display: none;
}


//MIXINS
@mixin button {
    display: block;
    text-align: center;
    text-decoration: none;
    width: 200px;
    color: $stark;
    -moz-border-radius: 20px;
    -webkit-border-radius: 20px;
    border-radius: 25px;
    font-size: 1em;
    padding-top: 7.5px;
    height: 40px;
}

@mixin button--hover {
    text-decoration: none;
    color: $stark;
}

@mixin clear {
    content: "";
    display: block;
    clear: both;
}


body {
    box-sizing: border-box;
    box-sizing: inherit;
    position: relative;
    background-color: $void;
    font-family: Oxygen, Arial, Helvetica, sans-serif;
    color: $stark;
}



//VISIBLE NAVIGATION
.navigation-bar {

    &__scroll {

    }
}

.navbar {
    font-size: 1em;
    padding-top: 1rem;
    list-style-type: none;
    display: block;
    &__icon {
        border-right: 2px solid $fuschia;
        margin-bottom: -4rem;
        padding-top: 18px;
        padding-bottom: 18px;
        padding-right: 1rem;
        &:hover {
            border-right: 2px solid $jewel;
        }
    }
    &__description {
        padding-left: 1rem;
        margin-top: 0.5rem;
        margin-left: 5rem;
        //visibility: hidden;
    }
    &__item {
        @include button--hover();
        &:hover {
            @include button--hover();
        }
    }
}



//DROPDOWN MENU
.dropdown-area {
    background-color: $fuschia;
    width: 20%;
    position: relative;
    display: none;
    &__button {
        position: absolute;
        left: 0;
        background-color: $void;
        @include button;
        &:hover {
            @include button--hover;
        }
    }
    &__how2play-button {
        position: absolute;
        left: 0;
        background-color: $void;
        @include button;
        &:hover {
            @include button--hover;
            background-color: $fuschia;
        }
    }
}

.dropdown {
    display: none;
    padding-top: 30px;
    text-align: right;
    &__listitem {
        @include button--hover;
        &:hover {
           @include button--hover; 
        }
    }
}



//HEADER
.header {
    position: relative;
    box-sizing: border-box;
    margin-bottom: 0;
    &__logo {
        width: 50px;
        height: 50px;
        margin-left: 15%;
        margin-top: 20%;
    }
    &__name {
        word-break: keep-all;
        white-space: nowrap;
        display: block;
        width: 100%;
        padding-left: 5%;
        padding-top: 5%;
        font-size: 200%;
        font-family: 'Lobster', cursive;
    }
    &__icon {
        width: 50px;
        height: 50px;
        margin-right: 5%;
        margin-top: 10%;
    }
}

.line {
     margin-top: 0;
     height: 12px;
     border: 0;
     -moz-box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
     -webkit-box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
     box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.5);
}




//SECTION: HEAD (WITH GAME)
.head {
    position: relative;
    &__how2play-button {
        position: absolute;
        right: 0;
        background-color: $fuschia;
        margin-top: 1rem;
        @include button;
        &:hover {
            @include button--hover;
            background-color: $jewel;
        }
    }
}

.game {
    border: 1px solid $stark;
    width: 100%;
    height: 10rem;
    &__item {

    }
}




//SECTION: INSTRUCTIONS
.instructions-content {
    &__paragraph {

    }
}




//SECTION: ABOUT
.about-me {
    &__name {
        text-align: center;
    }
    &__photo {
        width: 25%;
        margin-right: 5%;
        float: left;
        border: 1px solid black;
    }
    &__content {
        width: 70%;
        float: left;
    }
    &:after {
        @include clear;
    }
}



//SECTION: PROJECTS
.my-projects {
    &__name {
        text-align: center;
    }
}

.projects-list {
    list-style-type: none;
    &__item {
        width: 45%;
        float: left;
        margin-right: 5%;
        margin-bottom: 20px;
        &:after {
            @include clear;
        }
    }
    &__photo {
        height: 150px;
        overflow: hidden;
    }
    &__description {

    }
}



//SECTION: SKILLS
.skills {
    position: relative;
    &__name {
        padding-bottom: 10px;
        text-align: center;
    }
    &__description {
        display: block;
    }
}

.list-of-skills {
    display: block;
    list-style-type: none;
    &__item {
        display: block;
    }
    &__description {
        float: left;
        max-width: 90%;
    }
    &__icon {
        float: left;
        max-width: 5%;
        margin-left: 5%;
    }
    &:after {
        @include clear;
    }
}



