    &__mail {

    }
    &__message {

    }
    &__btn {

    }
}



//SCROLL
.navigation-scroll {
    &__button {

    }
}



//FOOTER
.footer {
    position: relative;
    &__content {
        position: absolute;
        font-family: 'Lobster', cursive;
        padding-top: 1rem;
    }
}

